.scoop {
	width: 100px;
	height: 80px;
	border-radius: 100%;
	position: relative;
	margin-bottom: -45px;
	z-index: 3;
	&::after {
		content: "";
		position: absolute;
		bottom: 10px;
		left: -10px;
		height: 35px;
		width: 120px;
		border-radius: 100%;
		z-index: 2;
	}
	&:first-of-type {
		margin-bottom: 0px;
	}
	&:hover {
		transform: scale(1.1);
	}
}
