@import "partials/variables";
@import "partials/mixins";
@import "partials/classes";

* {
	box-sizing: border-box;
	font-size: 14px;
	padding: 0px;
	margin: 0;
}

body {
	@include theme(background, $color-light-bg, $color-dark-bg);
	@include theme(color, $color-dark, $color-light);
	font-family: $font-base-family;
	font-size: $font-base-size;
	overflow-y: hidden;
}

a {
	text-decoration: none;
	&:link {
		@include theme(color, $color-primary, $color-tan);
	}
	&:visited {
		color: $color-success;
	}
	&:hover {
		@include theme(color, $color-dark, $color-light);
	}
	&:active {
		color: $color-primary;
	}
}

.icecream {
	display: flex;
	flex-direction: column-reverse;
	justify-content: center;
	align-items: center;
	height: calc(100vh - 40px);
	width: 100%;
}

.cone {
	margin-top: -20px;
	height: 0px;
	width: 0px;
	border: {
		right: 45px solid transparent;
		left: 45px solid transparent;
		top: 140px solid $color-tan;
	}
	z-index: 1;
}

.cherry {
	background: url(../assets/cherry.png) center/cover no-repeat;
	width: 50px;
	height: 77px;
	z-index: 3;
	margin-bottom: -10px;
}

.chocolate {
	@extend .scoop;
	background: $color-chocolate;
	&:after {
		background: $color-chocolate;
	}
}

.vanilla {
	@extend .scoop;
	background: $color-vanilla;
	&:after {
		background: $color-vanilla;
	}
}

.strawberry {
	@extend .scoop;
	background: $color-strawberry;
	&:after {
		background: $color-strawberry;
	}
}

.orange {
	@extend .scoop;
	background: $color-orange;
	&:after {
		background: $color-orange;
	}
}

.lemon {
	@extend .scoop;
	background: $color-lemon;
	&:after {
		background: $color-lemon;
	}
}
